import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { Modal } from "react-bootstrap";
import StickyBox from "react-sticky-box";
import { useTranslation } from "react-i18next";
import CircularProgress from "@mui/material/CircularProgress";
import format from "date-fns/format";
import AppointmentService from "../../../services/appointment.services";
import AuthService from "../../../services/auth.service";
import DashboardSidebar from "../sidebar/sidebar.jsx";
import { NoDataFound } from "common/components";

const Appointments = () => {
  const [t, i18n] = useTranslation();
  const user = AuthService.getCurrentUser();
  const sleep = (ms) => new Promise((resolve) => setTimeout(resolve, ms));

  const [appointmentsList, setAppointmentsList] = useState();
  const [isActionLoading, setIsActionLoading] = useState(false);
  const [loading, setLoading] = useState(false);
  const [recordId, setRecordId] = useState();
  const [action, setAction] = useState();
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  const appointmentAction = async (id, action) => {
    console.log(`appointmentAction - START : ${id} - ${action}`);
    setAction(action);
    setIsActionLoading(true);
    setRecordId(id);
    await sleep(1500);
    const response = await AppointmentService.updateAppointment(id, {
      status: action,
    });
    // setMessage(!!response.message && response.message);
    setIsActionLoading(false);

    if (!!!response.success && !response.success) return;

    // setIsActionLoading(false);
    console.log(`appointmentAction - END`);
  };

  useEffect(async () => {
    setLoading(true);
    // const userId = user?.roles[0] != "ROLE_ADMIN" ? user.id : null;
    // const response = await AppointmentService.getAllAppointments(userId);
    const response = await AppointmentService.getAllAppointments();
    setLoading(false);
    setAppointmentsList(response?.body);
  }, [isActionLoading]);

  function capitalizeFirstLetter(string) {
    return string.charAt(0).toUpperCase() + string.slice(1);
  }

  return (
    <div>
      <div className="content">
        <div className="container-fluid">
          <div className="row">
            <div className="col-md-5 col-lg-4 col-xl-3 theiaStickySidebar">
              <StickyBox offsetTop={20} offsetBottom={20}>
                <DashboardSidebar />
              </StickyBox>
            </div>

            <div className="col-md-7 col-lg-8 col-xl-9">
              <div className="card">
                <div className="card-header">
                  <h4 className="card-title">{t("Appointments")}</h4>
                </div>
                {loading ? (
                  <div className="centered">
                    <h4>
                      <div>
                        <CircularProgress />
                      </div>
                      <div>
                        <span style={{ color: "#15558d" }}>
                          {t("AppointmentsComponent.GettingAppointments")}
                        </span>
                      </div>
                    </h4>
                  </div>
                ) : (
                  <div className="card-body">
                    <div className="card card-table mb-0">
                      <div className="card-body">
                        <div className="table-responsive">
                          <table className="table table-hover table-center mb-0">
                            <thead>
                              <tr>
                                <th>#</th>
                                <th>Patient Email</th>
                                <th>Creation Date</th>
                                <th hidden>Status</th>
                                <th hidden className="text-center">Action</th>
                              </tr>
                            </thead>
                            <tbody>
                              {appointmentsList?.length ? (
                                appointmentsList
                                  ?.filter((app) => app.status !== "deleted")
                                  .map((a) => (
                                    <tr key={a.id}>
                                      <td>
                                        <Link
                                          to="/account-appointment-details"
                                          state={a}
                                        >
                                          {a.id}
                                        </Link>
                                      </td>
                                      <td>
                                        <Link
                                          to="/account-appointment-details"
                                          state={a}
                                        >
                                          {a.patientEmail}
                                        </Link>
                                      </td>
                                      {/* <td>
                                        <h2 className="table-avatar">
                                          <Link to="/patient/doctor-profile">
                                            Dr.{" "}
                                            {`${a?.doctorFirstName} ${
                                              !!a?.doctorLastName
                                                ? a?.doctorLastName
                                                : ""
                                            }`}{" "}
                                            <span>Dental</span>
                                          </Link>
                                        </h2>
                                      </td> */}
                                      <td>
                                        {format(
                                          new Date(a.createdDate),
                                          "dd/MM/yyyy"
                                        )}{" "}
                                      </td>
                                      {/* <td>12 Nov 2019</td> */}
                                      <td hidden>
                                        <span
                                          className={`badge badge-pill bg-${
                                            a?.status === "confirmed"
                                              ? "success"
                                              : a?.status === "pending" ||
                                                a?.status === null
                                              ? "warning"
                                              : "danger"
                                          }-light`}
                                        >
                                          {capitalizeFirstLetter(
                                            !!a?.status ? a?.status : "pending"
                                          )}
                                        </span>
                                      </td>
                                      <td className="text-center" hidden>
                                        <div className="table-action">
                                          <button
                                            className="btn btn-sm bg-success-light"
                                            onClick={(e) =>
                                              appointmentAction(
                                                a?.id,
                                                "confirmed"
                                              )
                                            }
                                            disabled={isActionLoading}
                                          >
                                            <i className="fas fa-check" />{" "}
                                            Confirm
                                            {action === "confirmed" &&
                                              recordId === a?.id &&
                                              isActionLoading && (
                                                <span className="spinner-border spinner-border-sm mb-1 mx-1"></span>
                                              )}
                                          </button>
                                          <button
                                            className="btn btn-sm bg-danger-light"
                                            onClick={(e) =>
                                              appointmentAction(
                                                a.id,
                                                "rejected"
                                              )
                                            }
                                            disabled={isActionLoading}
                                          >
                                            <i className="fas fa-times" />{" "}
                                            Reject
                                            {action === "rejected" &&
                                              recordId === a?.id &&
                                              isActionLoading && (
                                                <span className="spinner-border spinner-border-sm mb-1 mx-1"></span>
                                              )}
                                          </button>
                                          <button
                                            className="btn btn-sm bg-light"
                                            onClick={(e) =>
                                              appointmentAction(a.id, "deleted")
                                            }
                                            disabled={isActionLoading}
                                          >
                                            {action === "deleted" &&
                                            recordId === a?.id &&
                                            isActionLoading ? (
                                              <span className="spinner-border spinner-border-sm mb-1 mx-1"></span>
                                            ) : (
                                              <i className="fas fa-trash-alt" />
                                            )}
                                          </button>
                                        </div>
                                      </td>
                                    </tr>
                                  ))
                              ) : (
                                <NoDataFound colSpan={5} />
                              )}
                            </tbody>
                          </table>
                        </div>
                      </div>
                    </div>
                  </div>
                )}
              </div>
            </div>
          </div>
        </div>
      </div>

      <Modal
        show={show}
        onHide={handleClose}
        aria-labelledby="example-modal-sizes-title-sm"
      >
        <Modal.Header>
          <h5 className="modal-title">Add new data</h5>
          <button
            type="button"
            className="close"
            data-dismiss="modal"
            aria-label="Close"
            onClick={handleClose}
          >
            <span aria-hidden="true">×</span>
          </button>
        </Modal.Header>
        <Modal.Body>
          <div className="modal-body">
            <div>
              <form
                action="#"
                enctype="multipart/form-data"
                autocomplete="off"
                method="post"
              >
                <div className="modal-body">
                  <input type="hidden" defaultValue name="id" />
                  <input type="hidden" defaultValue="insert" name="method" />
                  <div className="form-group">
                    <label className="control-label mb-10">
                      {" "}
                      BMI <span className="text-danger">*</span>
                    </label>
                    <input
                      type="text"
                      name="bmi"
                      className="form-control"
                      defaultValue="23.7"
                    />
                  </div>
                  <div className="form-group">
                    <label className="control-label mb-10">Heart rate </label>
                    <input
                      type="text"
                      name="hr"
                      className="form-control"
                      defaultValue={89}
                    />
                  </div>
                  <div className="form-group">
                    <label className="control-label mb-10">Weight</label>
                    <input
                      type="text"
                      name="Weight"
                      className="form-control"
                      defaultValue={74}
                    />
                  </div>
                  <div className="form-group">
                    <label className="control-label mb-10">Fbc</label>
                    <input
                      type="text"
                      name="Fbc"
                      className="form-control"
                      defaultValue={140}
                    />
                  </div>
                  <div className="form-group">
                    <label className="control-label mb-10">Creation Date </label>
                    <input
                      type="text"
                      name="dob"
                      id="editdob"
                      defaultValue="11/11/2019"
                      readOnly
                      className="form-control"
                    />
                  </div>
                </div>
                <div className="modal-footer text-center">
                  <button
                    type="submit"
                    className="btn btn-outline btn-success "
                  >
                    Submit
                  </button>
                </div>
              </form>
            </div>
          </div>
        </Modal.Body>
      </Modal>
    </div>
  );
};
export default Appointments;
